import * as THREE from 'three'
import { FontLoader } from 'three/src/loaders/FontLoader'
import { TextGeometry } from 'three'

export class Text2Mesh {
    public static async CreateText (text: string) : Promise<THREE.Mesh> {
        const loader = new FontLoader()
        const font = await loader.loadAsync('https://raw.githubusercontent.com/mrdoob/three.js/master/examples/fonts/optimer_regular.typeface.json')
        const textGeo = new TextGeometry(text, {
            font: font,
            size: 0.5,
            height: 0.1,
            curveSegments: 10,
            bevelThickness: 1,
            bevelSize: 1
        })

        textGeo.computeBoundingBox()

        const materials = new THREE.MeshBasicMaterial()
        const mesh = new THREE.Mesh(textGeo, materials)

        return mesh
    }
}
